import Navbar from "./components/NavBar";
import { useState } from "react";
import { SearchContext, DarkContext } from "./components/NavBar";
import PageRoute from "./components/DataTable/PageRoute";
import { FavoriteContext } from "./components/DataTable/DataTable";
import { ThemeProvider } from "@material-ui/styles";
import { Container, createTheme, CssBaseline } from "@material-ui/core";
import { useListPostsQuery } from "./components/data";

function App() {
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(20);
  const { data: coins, isLoading } = useListPostsQuery({
    page: page + 1,
    limit: rowsPerPage,
  });

  const { data: allCoins } = useListPostsQuery({
    page: page + 1,
    limit: 100,
  });

  const [search, setSearch] = useState("");
  const [favorite, setFavorite] = useState([]);
  const [dark, setDark] = useState(false);

  const darkTheme = createTheme({
    palette: {
      type: dark ? "dark" : "light",
    },
  });

  return (
    <DarkContext.Provider value={setDark}>
      {!isLoading && (
        <ThemeProvider theme={darkTheme}>
          <CssBaseline />
          <SearchContext.Provider value={setSearch}>
            <Navbar
              search={search}
              favorite={favorite}
              mode={dark}
              changePage={setPage}
            />
          </SearchContext.Provider>
          <FavoriteContext.Provider value={setFavorite}>
            <PageRoute
              onChangeRowsPerPage={setRowsPerPage}
              rowsPerPage={rowsPerPage}
              coins={search === "" ? coins : allCoins}
              search={search}
              favorite={favorite}
              changePage={setPage}
              page={page}
            />
          </FavoriteContext.Provider>
        </ThemeProvider>
      )}
      {isLoading && (
        <p style={{ textAlign: "center", fontSize: "30px" }}>Loading...</p>
      )}
    </DarkContext.Provider>
  );
}

export default App;
